import React from 'react'
import { Technologies } from 'components/constant/Technology'

interface IProps {
    technology: string
}

class Card extends React.Component<IProps> {
    render() {
        const tech = Technologies[this.props.technology]
        return (
            <div className="card">
                <div className="image">
                    <img src={tech.getImage()} className="mx-auto d-block" />
                </div>
                <div className="title">{tech.getName()}</div>
            </div>
        )
    }
}

export default Card