class Technology {
    private name: string;
    private image: string;
    private url: string;

    constructor(name: string, image: string, url?:string) {
        this.name = name;
        this.image = image;
        this.url = url || "";
    }

    getName() {
        return this.name
    }

    getImage() {
        return this.image
    }

    getUrl() {
        return this.url
    }
}

export default Technology